    p.mb-5(data-aos="flip-up") El mapa de navegacin es un recurso, esquema o elemento grfico por lo general en forma de rbol, que representa la estructura o arquitectura del sistema. Este tipo de recursos es usado para poder optimizar los diseos y tener una adecuada estructuracin de los contenidos, previamente a desarrollar los contenidos de la aplicacin #[i web] buscando que el usuario siempre acceda de forma sencilla a lo que est buscando. Sabiendo esto, ahora podrs conocer informacin relevante sobre los mapas de navegacin:

