      .h2 2.4. Procedimientos para disear un sitio #[i web]

    p.mb-5(data-aos="flip-up") En el proceso del diseo y desarrollo de un sitio #[i web], es importante tener presente los siguientes aspectos: 

    AcordionAA.mb-5.acordion(tipo="a" clase-tarjeta="tarjeta bg_05")(data-aos="flip-up")
      .row.justify-content-center(titulo="Realizar una planificacin adecuada")
        p Es importante tener claro cul es el objetivo y que se quiere construir antes de iniciar. Un producto #[i software] puede tener muchas utilidades y segn las especificaciones de los requisitos de #[i software] habr una u otra estructura que se acople mejor a la necesidad. Teniendo en cuenta caractersticas como el objetivo (#[i target]), la informacin a presentar, las palabras clave, formulario de acceso, recursos multimedia, gama de colores, tipografa, entre otros. 

        p Algunas categorizaciones:
        ul.lista-ul--color.li_1
          li(data-aos="slide-left") 
            i.fas.fa-check
            p.mb-0 Aplicaciones #[em web]
