            i.fas.fa-check
            | #[em Landing page]
          li(data-aos="slide-left") 
            i.fas.fa-check
            | #[em E-Commerce]
          li(data-aos="slide-right") 
            i.fas.fa-check
            p.mb-0 Pgina #[em web] de servicios
          li(data-aos="slide-left") 
            i.fas.fa-check
            p.mb-0 Porfolios / Pagina <em> web</em> personal 
      .row.justify-content-center(titulo="Establece el dominio y selecciona el hosting")
        p Sin estos dos elementos es muy poco probable que tu producto #[i software] #[i web] pueda estar en lnea, dado que el #[i hosting] es el espacio donde se aloja el producto y todos los recursos que lo conforman, en pocas palabras es el espacio en el cual est desplegado el producto. Dependiendo de la complejidad del proyecto se podr escoger un #[i hosting] bsico u otro ms complejo que d respuesta a las necesidades de infraestructura. 

        p Mientras, el dominio es el nombre que asignamos a la #[i web] este tambin es conocido como DNS (#[i Domain Name Server]) por sus siglas en ingls y su funcin consiste en traducir la direccin IP para encontrarla fcilmente en internet. Es importante que el nombre del dominio sea una insignia del sitio #[i web], dado que ser un identificador para que los usuarios tengan mayor recordacin. Algunos ejemplos de dominio son google.com, facebook.com, gmail.com, instagram.com, gov.com, entre otros. 
      .row.justify-content-center(titulo="Elige el lenguaje de programacin para el desarrollo de tu sitio <em>web</em>")
        p Si tu sitio #[i web] es un sistema de informacin a la medida, lo ms probable es que el desarrollo se realice desde cero, por eso es importante identificar cual es la arquitectura de aplicacin ms apropiada para la solucin, identificar los pro y contra del uso de uno u otro lenguaje y de ser necesario explorar un #[i framework] que se adapte a la necesidad. 

        p Despus de realizar la identificacin de la arquitectura de aplicacin, se debe seleccionar un lenguaje de programacin, explorar los posibles #[i frameworks], comenzar la instalacin del entorno de desarrollo, lo que incluye instalar los sdks (#[i Software Development Kit]) necesarios, instalar un servidor de aplicaciones, seleccionar un motor de base de datos para almacenar la informacin requerida, instalar el IDE y #[i plugins], configurar una herramienta el control de las versiones de cdigo y comenzar a codificar la solucin.

        p Si se identifica que la necesidad es genrica y que en el mercado se cuenta con herramientas que permitan construir un sitio #[i web] por medio de un CMS (#[i Content Management System]), los pasos descritos previamente no sern necesarios y el enfoque estar en escoger bien qu componentes usar para iniciar el sitio #[i web].
      .row.justify-content-center(titulo="Elige el CMS idneo para tu pgina <em>web</em> y realiza la instalacin ")
        p Estas herramientas de diseo en tres clics, se encuentran una serie de gestores de contenidos que permiten crear pginas #[i webs] desde cero con buenos resultados, entre ellos se puede destacar WordPress como una de las mejores opciones para crear una pgina #[i web].

        p Drupal y magneto son CMS, reconocidos y potentes con los cuales se pueden crear sitios #[i web]. Otro ejemplo son Prestashop o Shopify que estn enfocados en la creacin de tiendas #[i online]. Un CMS bastante usado es WordPress, ya que es un gestor sencillo al momento de implementar los componentes #[i web] con una amplia documentacin y comunidad.

        p Un valor agregado de los CMS, es que cuentan con una comunidad de desarrolladores que construyen #[i plugins] y componentes para diferentes necesidades, con lo cual el propietario del sitio solo es ir al panel de control y aadir el componente que desea para comenzar a agregar nuevas funcionalidades a su sitio #[i web]. 
      .row.justify-content-center(titulo="Establece la estructura <em>web</em>")
        p En este apartado se hace referencia a la estructuracin, distribucin y organizacin de los elementos que compondrn la estructura visual del componente #[i web], de la cual se crea la diagramacin del sitio y se establece como ver la informacin el usuario, tambin se incluye la relacin de las diferentes pginas entre s, cules sern las superiores, cuales tendrn dependencias de otras, cules sern pblicas, cules requerirn de un inicio de sesin, cules tendrn su representacin en la parte superior (header) o en la parte inferior (footer). Al hablar de diseo #[i web], no solo se hace referencia a la parte visual, dado que la arquitectura interna tambin es importante y dentro de esa arquitectura se puede tener una arquitectura especfica por el #[i front-end], por el #[i back-end] y una genrica de sistema. 
      .row.justify-content-center(titulo="Define un diseo para el sitio <em>web</em>")
        p En este punto es requerido definir varios elementos que estn relacionados con las plantillas #[i web], es importante tener como referencia el tipo de informacin que se refleja y el tipo de usuario que tendr acceso a la informacin. Imagina que ests diseando un producto #[i software] para una escuela donde se orientan clases a nios de primero hasta quinto de primaria, donde las edades oscilan entre los 7 a 12 aos, el requisito es que se construya una aplicacin #[i web] que por medio de juegos y se pueda tener un recurso didctico adicional. 

        p Por lo cual jugar con colores llamativos, letra grande, tipografa animada, muchas imgenes har que los nios olviden que estn estudiando y se enfoquen en aprender jugando, pero si al contrario viras ese diseo a sitios empresariales orientado a personas mayores, posiblemente no se le sacar el mayor provecho. 

