      .h2 2.5 Lenguajes de programacin, #[i frameworks, IDEs] y #[i plugins] usados en el desarrollo #[i web]

    p(data-aos="flip-up") Un lenguaje de programacin es un lenguaje formal, en pocas palabras es un lenguaje con reglas gramaticales bien definidas, que proporciona a una persona la capacidad de escribir una serie de instrucciones (programar) con el fin de controlar el comportamiento de un componente informtico, tales como: los computadores, smartphones, microcontroladores, Smart Tv, entre otros. 

    .row.bg10.px-5.py-3.mb-3
      .col-lg-12
        p.mb-4(data-aos="flip-up") En la actualidad, hay una serie de lenguajes que se usan en diferentes frentes o reas de la programacin; por ejemplo, para desarrollar #[i front-end] existe JavaScript en conjunto con HTML5 y Css3, para el desarrollo Mobile dependiendo si es iOS se usa Swift, y en su contraparte Android se usa Java o Kotlin, en el desarrollo #[i back-end] podemos encontrar lenguajes como Java, PHP, Python, C#, C++. 

    .row.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="slide-left") 
        p Por otro lado, los #[i Frameworks] (Marcos de trabajo) son una serie de libreras, paquetes y clases, que buscan optimizar el proceso de desarrollo, por ejemplo, en JavaScript podemos encontrar a Angular, VueJs, ReactJs, en PHP encontramos a Yii, Cake, Laravel, Java a Spring MVC, Spring Boot, PrimeFaces. Estos fueron diseados con la finalidad de implementar patrones identificados y maximizar el proceso de configuracin y desarrollo inicial de los productos #[i software], de tal manera que los equipos de trabajo se enfocarn explcitamente en la lgica de negocio relacionada al problema. 
