            h4 JavaScript
            p Fue desarrollado en 1995 por Netscape, su objetivo inicial era maximizar los lenguajes HTML y CSS para que los programadores sepan cmo interactan los usuarios dentro de una pgina #[i web] y as poder presentar el contenido de forma ms dinmica. El crecimiento de JavaScript dentro del sector ha sido tan grande que su uso no se limit a los navegadores #[i web], sino que tambin se aplica a nivel de servidor. #[strong #[i Frameworks]]: Angular, Vue.js, ReactJs, Ember.js, Meteoro, Mithril, Node.js, Polmero, Aurelia, Backbone.js.
      .py-4.py-md-5(titulo=" " :icono="require('@/assets/curso/tema_2/img_12.png')")
        .row
          .col-md-8.mb-4.mb-md-0.bg_06.p-4
            h4 PHP
            p Este lenguaje gratuito sirve para aquellos programadores que empiezan a manejarse en la creacin de pginas #[i web] y aplicaciones #[i web] dinmicas, gracias a su facilidad de comprensin y compatibilidad con el lenguaje esttico HTML, est muy vinculado a la creacin y mantenimiento de bases de datos y, por supuesto, sirve de soporte a WordPress, el CMS ms usado en la actualidad por los desarrolladores #[i web] para crear sus proyectos o gestionar las #[i webs] de sus clientes. #[i Frameworks]: Aura, FuelPHP, Slim, Phalcon, CakePHP, Zend Framework, Yii, Codeigniter.
      .py-4.py-md-5(titulo=" " :icono="require('@/assets/curso/tema_2/img_13.png')")
        .row
          .col-md-8.mb-4.mb-md-0.bg_06.p-4
            h4 Java
            p Es un lenguaje de programacin comercializado en 1995 por Sun Microsystems y desde aquella poca ha crecido hasta consolidarse en pleno siglo XXI como la referencia a la hora de crear pginas #[i web], de hecho, si no tienes Java instalado en tu ordenador seguramente no podrs ver por completo la gran mayora de #[i webs] que visitas hoy en da. #[i Frameworks]: Spring, Spring-boot, Puntales, Primefaces, Hibernate, Apache Wicket, JSF (Java Server Faces), Dropwizard, Griales, ATG, Apache Hadoop.
      .py-4.py-md-5(titulo=" " :icono="require('@/assets/curso/tema_2/img_14.png')")
