            h4 Ruby
            p Es un lenguaje de programacin que ofrece escritura dinmica y un recolector de basura automtico, es decir, es el propio #[i software] el que utiliza de forma eficiente la memoria, liberando espacio cuando ya no tiene sentido mantener todo aquello que est dentro de dicho espacio. Una gran ventaja que ofrece Ruby es que es muy fcil de leer, pero hay que tener cuidado a la hora de cometer errores cuando se est programando, porque cualquier palabra mal escrita puede derivar en tiempos de carga muy lentos. #[strong #[i Frameworks]]: Sinatra, Hobbit, Ramaze, Nancy, Padrino, Crepe, NYNY, Ruby Grape,Celluloid, Hanami, Scorched.
      .py-4.py-md-5(:icono="require('@/assets/curso/tema_2/img_15.png')")
