            h4 Python
            p Es otro lenguaje gratuito que no reporta gran dificultad para programar en cualquier sistema operativo, es dinmico y se utiliza como lenguaje de scripting. Es adems ampliamente reconocido en el sector tecnolgico porque diversos servicios del gigante Google como YouTube usan Python, al igual que muchos videojuegos incluyen en su programacin parte de cdigo escrito en Python. #[strong #[i Frameworks]]: #[i Django, CherryPy, Pirmide, Grok, TurboGears, Web2Py, Matraz, Flask] 
