        .row
          .col-md-8.mb-4.mb-md-0.bg_06.p-4
            h4 C#
            p Est considerado como uno de los lenguajes obligatorios que todo desarrollador o programador debe conocer si quiere tener proyectos a su cargo en todo momento. A pesar de recibir ciertas crticas con base a la vinculacin entre C# y .NET Framework, la gran ventaja es que aprender y empezar a programar en C# permite estar presente en Windows, el sistema operativo ms usado del mundo, adems de poder aprovechar las sinergias que Microsoft ha creado entre Windows y su consola de sobremesa Xbox. #[strong #[i Frameworks]:] .Net MVC, .Net Core

    p.mb-5(data-aos="flip-up") Por su parte, un IDE (Integrated Developer Environment) o entorno de desarrollo integrado, es una aplicacin especial diseada para programar, la cual cuenta con una interfaz grfica (GUI), con un editor de texto que permite escribir las sentencias u rdenes en el lenguaje de programacin seleccionado para construir el producto #[i software]. Este tipo de herramientas vienen con una serie de pasos de configuracin para que al realizar el proceso de programacin, compilacin y ejecucin todo est acorde y se pueda probar y desplegar el producto de manera local (en la mquina del desarrollador). Sabiendo esto, las siguientes son algunas de las caractersticas de los IDE:

    .cajon.color-acento-botones.p-4.mb-5(data-aos="slide-left") 
      p.mb-0 #[strong.recua_bg1 Editor de cdigo fuente:] es la herramienta que permite al desarrollador escribir el cdigo fuente del producto #[i software], esta herramienta por lo general contiene indicadores visuales, que ayudan a identificar los errores de sintaxis, autocompletado, comprobacin de errores a la medida que se van escribiendo las sentencias. En esta herramienta se hace plena validacin de las reglas semnticas del lenguaje en el cual se est construyendo el producto #[i software].

    .cajon.color-secundario.p-4.mb-5(data-aos="slide-right") 
      p.mb-0 #[strong.recua_bg2 Automatizacin de compilaciones locales:] herramienta que sirve para automatizar tareas sencillas y repetitivas como parte de la comisin local, esto permite crear el cdigo binario, ejecucin de prueba automatizadas, verificacin de cobertura, verificacin de estilos en la programacin para el cdigo fuente. 

    .cajon.color-acento-botones.p-4.mb-5(data-aos="slide-left") 
      p.mb-0 #[strong.recua_bg1 Depurador:] es la herramienta que sirve para probar los programas, en el cual se realiza una inspeccin y se ubican los errores en el cdigo de forma visual o en un reporte en texto plano, esto es de mucha utilidad para los desarrolladores ya que permite encontrar problemas rpidamente y dar solucin. 

