    p.mb-5 A su vez, los #[strong #[i plugins]] son componentes que permiten habilitar nuevas caractersticas a los IDE y entornos de desarrollo. Utilizar #[i plugins] implica asumir cierto riesgo en la seguridad, ya que los hackers pueden usar estas ampliaciones como puerto de entrada para un ataque en el computador del desarrollador, esto se puede dar, dado que los #[i plugins] son parches de #[i software] desarrollados por agentes externos al propietario del IDE, por lo cual podra contener en su cdigo fuente un malware que se use con fines adversos al inicialmente definido por el entorno de desarrollo. Tener en cuenta que cada IDE tiene su propia lnea de #[i plugins], con lo cual el desarrollador dispondr de una amplia gama de opciones para definir cul ser la mejor herramienta para poder desempear sus funciones. 

